<template>
    <div class="result page">
        <h1>Your practice is finished</h1>
        <ul>
            <li>
                <div>Correct Questions</div> <div>{{ correctQuestions.length }} / {{ getSet.length }}</div>
            </li>
            <li>
                <div>Wrong Questions</div> <div>{{ wrongQuestions.length }} / {{ getSet.length }}</div>
            </li>
            <li>
               <div>Status</div>
               <div>
                   <span v-if="correctQuestions.length >= (getSet.length / 2)">Passed</span>
                   <span v-else>Failed</span>
                   ({{ Math.round(((correctQuestions.length * 100) / getSet.length)) }}%)</div>
            </li>
        </ul>
        <router-link to="/review">
            <button>Review your answers</button>
        </router-link>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Finish",
  methods: mapActions(["reset"]),
  computed: mapGetters(["getSet", "correctQuestions", "wrongQuestions"])
};
</script>

<style lang="sass">
.result
    ul
        margin: 2rem 0
        border: 1px solid #5cbfe5
        li
            display: flex
            div
                display: flex
                flex-direction: column
                justify-content: center
                padding: 1rem
                border: 1px solid #5cbfe5
                &:first-of-type
                    width: 67%
                &:last-of-type
                    width: 33%
    button
        position: fixed
        font-size: 1rem
        left: 0
        bottom: 0
        width: 100%
        background: #5cbfe5
        color: white
        padding: 1rem
        border: none
</style>

