<template>
    <div class="progress-container">
        <div class="progress-bar" :style="`width: ${getProgress + 1}%`"></div>
        {{ getSelectedQuestionIndex + 1 }}/{{ getSet.length }}
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ProgressBar",
  computed: mapGetters([
    "allQuestions",
    "getSet",
    "getSelectedQuestionIndex",
    "getProgress"
  ])
};
</script>

<style lang="sass">
.progress-container
  position: fixed
  left: 0
  top: 0
  background: #b4eaff
  color: black
  padding: .125rem
  width: 100%
  text-align: center
  .progress-bar
    position: absolute
    z-index: -1
    top: 0
    left: 0
    width: 0%
    height: 100%
    background: #5cbfe5
    transition: width .5s linear
</style>
