import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    name: "",
    loading: true,
    //questions: [],
    selectedSet: [],
    correctQuestions: [],
    wrongQuestions: [],
    selectedQuestionIndex: 0,
    nextActive: true,
    discipline: "",
    practice: {},
    set: [],
    progress: 0,

    data: {}
  },
  getters: {
    //getQuestions: state => state.questions,
    getData: state => state.data,
    getName: state => state.name,
    getLoading: state => state.loading,
    allQuestions: state => state.questions,
    getSelectedSet: state => state.selectedSet,
    correctQuestions: state => state.correctQuestions,
    wrongQuestions: state => state.wrongQuestions,
    getSelectedQuestionIndex: state => state.selectedQuestionIndex,
    getSelectedQuestion: state => state.set[state.selectedQuestionIndex],
    nextActive: state => state.nextActive,
    getDiscipline: state => state.discipline,
    getPractice: state => state.practice,
    getSet: state => state.set,
    getProgress: state => state.progress
  },
  actions: {
    async setData({ commit }) {
      const response = await axios.get(
        `/api/questions/${this.state.discipline.toLowerCase()}`
      );
      commit("setData", response.data);
    },

    async fetchQuestions({ commit }) {
      const { discipline, lesson } = this.state;
      await axios
        .get(`/api/questions/${discipline}/${lesson}`)
        .then(response => {
          commit("populateQuestions", response.data);
          commit("toggleLoading");
        });
    },
    storeName({ commit }, name) {
      if (name !== "") {
        commit("setName", name);
      }
    },
    chooseSet({ commit }, set) {
      commit("chooseSet", set);
    },
    correctAnswer({ commit }, question) {
      commit("addCorrectQuesiton", question);
    },
    wrongAnswer({ commit }, question) {
      commit("addWrongQuestion", question);
    },

    setDiscipline({ commit }, discipline) {
      commit("setDiscipline", discipline);
    },

    reset({ commit }) {
      commit("reset");
    }
  },
  mutations: {
    setData: (state, data) => {
      state.data = data;
    },

    setName: (state, name) => (state.name = name),
    populateQuestions: (state, data) => (state.questions = data),
    chooseSet: (state, data) => (state.selectedSet = data),
    addCorrectQuesiton: (state, question) =>
      state.correctQuestions.push(question),
    addWrongQuestion: (state, question) => state.wrongQuestions.push(question),
    nextQuestion: state => {
      if (state.set.length !== 0) {
        if (state.selectedQuestionIndex == state.set.length - 2) {
          state.nextActive = false;
        }
        if (!(state.selectedQuestionIndex == state.set.length - 1)) {
          state.selectedQuestionIndex += 1;
        }
        state.progress =
          ((state.selectedQuestionIndex + 1) / state.set.length) * 100;
      }
      // } else {
      //   if (state.selectedQuestionIndex == state.questions.length - 2) {
      //     state.nextActive = false;
      //   }
      //   if (!(state.selectedQuestionIndex == state.questions.length - 1)) {
      //     state.selectedQuestionIndex += 1;
      //   }

      //   state.progress =
      //     ((state.selectedQuestionIndex + 1) / state.questions.length) * 100;
      // }
    },

    setDiscipline: (state, discipline) => (state.discipline = discipline),

    setPractice: (state, practice) => {
      state.practice.topic = practice.topic;
      state.practice.practice = practice.practice;
    },

    setSet: (state, set) => (state.set = set),

    toggleLoading: state => (state.loading = !state.loading),

    reset: state => {
      state.questions = [];
      state.selectedSet = [];
      state.correctQuestions = [];
      state.wrongQuestions = [];
      state.selectedQuestionIndex = 0;
      state.nextActive = true;
      state.discipline = "";
      state.lesson = "";
      state.progress = 0;
    }
  }
});
