<template>
    <div class="practice page">
      <h1>{{ getDiscipline }} practice</h1>
        <template v-for="(practice, index) in getData">
          <h3 :key="practice.topic" v-text="`${practice.topic}:`"></h3>
          <div class="qboxes" :key="index">
            <QBox :key="practice.title" :topic="practice.topic" :title="practice.title" :questions="practice.questions.length" />
          </div>
        </template>
    </div>
</template>

<script>
import QBox from "@/components/QBox";

import { mapGetters, mapActions } from "vuex";

export default {
  name: "Lessons",
  components: {
    QBox
  },
  methods: {
    ...mapActions(["setData"])
  },
  computed: {
    ...mapGetters(["getDiscipline", "getData"])
  },
  created() {
    this.setData();
  }
};
</script>

<style lang="sass" scoped>
.practice
  h3
    font-size: 1.25rem
    margin-top: 2rem
    margin-bottom: 1rem  
  .qboxes
    display: flex
    flex-wrap: wrap
</style>
