<template>
  <div class="question">
    <h2 v-text="getSelectedQuestion.text"></h2>
      <form @submit.prevent>
        <ol ref="list">
          <li v-for="(choice, index) in getSelectedQuestion.choices" :key="index">
            <input
              v-model="chosen"
              type="radio"
              name="choice"
              :id="`choice_${index}`"
              :value="choice.text">
            <label
              :for="`choice_${index}`"
              v-text="choice.text"
              @click="indicateSelection($event)"></label>
            <div class="overlay"></div>
          </li>
        </ol>
        <button @click="saveAndNext(getSelectedQuestion)" v-if="nextActive">Next Question</button>
        <button @click="saveAndFinish(getSelectedQuestion)" v-else>Finish Exam</button>
      </form>
  </div>
</template>

<script>
import store from "@/store/index.js";

import { mapGetters } from "vuex";
import { mapActions } from "vuex";
//import axios from "axios";

export default {
  name: "Question",
  data() {
    return {
      chosen: ""
    };
  },
  methods: {
    ...mapActions(["correctAnswer", "wrongAnswer", "nextQuestion"]),
    indicateSelection(event) {
      this.$refs.list.children.forEach(li => li.classList.remove("selected"));
      event.path[1].classList.add("selected");
    },
    saveAnswer(question) {
      const findCorrect = () => {
        return question.choices.find(choice => choice.correct == true);
      };
      // add chosen property
      question.choices.find(choice => choice.text === this.chosen)[
        "chosen"
      ] = true;

      if (this.chosen == findCorrect().text) {
        this.correctAnswer(question);
      } else {
        this.wrongAnswer(question);
      }
    },
    saveAndNext(question) {
      this.$refs.list.children.forEach(li => li.classList.remove("selected"));
      this.saveAnswer(question);
      store.commit("nextQuestion", question);
    },
    saveAndFinish(question) {
      this.saveAnswer(question);
      this.$router.push("finish");
    }
  },
  computed: mapGetters(["getSelectedQuestion", "nextActive"])
};
</script>

<style lang="sass" scoped>
.question
  margin: 2rem 0
  ol
    list-style: lower-latin
    li
      margin-bottom: 1rem
      padding: .5rem
      transition: color .25s ease-in-out
      .overlay
        width: 0
      &.selected
        position: relative
        color: white
        .overlay
          position: absolute
          z-index: -1
          left: -2rem
          top: 0
          width: calc(100% + 2rem)
          height: 100%
          background: #5cbfe5
          transition: width .25s linear
      input
        display: none
  button
    position: fixed
    font-size: 1rem
    left: 0
    bottom: 0
    width: 100%
    background: #5cbfe5
    color: white
    padding: 1rem
    border: none
</style>
