<template>
    <div class="setbox" @click="toExam()">
        <div class="number">
            <div class="text">Questions</div>
            <div class="range" v-text="range"></div>
            <div class="title" v-text="title"></div>
        </div>
    </div>
</template>

<script>
import store from "@/store/index.js";

export default {
  name: "SetBox",
  props: {
    title: {
      type: String
    },
    range: {
      type: String
    },
    set: {
      type: Array
    }
  },
  methods: {
    toExam() {
      store.commit("setSet", this.set);
      this.$router.push("/exam");
    }
  }
};
</script>

<style lang="sass" scoped>
.setbox
    background: white
    text-align: center
    width: calc(50% - .5rem)
    border-radius: .5rem
    margin-bottom: 1rem
    cursor: pointer
    position: relative
    &:nth-of-type(odd)
        margin-right: 1rem
    .number
        color: #5cbfe5
        margin-top: 1rem
        margin-bottom: 3rem
        .text
            font-size: 1.25rem
        .range
            font-size: 3rem
        .title
            position: absolute
            bottom: 0
            width: 100%
            text-align: center
            background: rgb(0, 0, 0, 0.66)
            color: white
            padding: .5rem 0
            border-bottom-left-radius: .5rem
            border-bottom-right-radius: .5rem
</style>
