<template>
    <div class="qbox" @click="toSets()">
        <div class="number">
            <div class="figure" v-text="questions"></div>
            <div class="text">Questions</div>
            <div class="title" v-text="title"></div>
        </div>
    </div>
</template>

<script>
import store from "@/store/index.js";

export default {
  name: "Qbox",
  props: {
    topic: {
      type: String
    },
    title: {
      type: String
    },
    questions: {
      type: Number
    }
  },
  methods: {
    toSets() {
      store.commit("setPractice", { topic: this.topic, practice: this.title });
      this.$router.push("sets");
    }
  }
};
</script>

<style lang="sass" scoped>
.qbox
    background: white
    text-align: center
    width: calc(50% - .5rem)
    border-radius: .5rem
    margin-bottom: 1rem
    cursor: pointer
    position: relative
    &:nth-of-type(odd)
        margin-right: 1rem
    .number
        color: #5cbfe5
        margin-top: .5rem
        margin-bottom: 3rem
        .figure
            font-size: 3rem
        .text
            font-size: 1.25rem
        .title
            position: absolute
            bottom: 0
            width: 100%
            text-align: center
            background: rgb(0, 0, 0, 0.66)
            color: white
            padding: .5rem 0
            border-bottom-left-radius: .5rem
            border-bottom-right-radius: .5rem
</style>
