<template>
    <div class="review page">
        <h1>Review your answers</h1>
        <div class="question" v-for="(question, index) in getSet" :key="question._id">
            <h3>{{ index + 1 }}. {{ question.text }}</h3>
            <ol>
                <li
                    v-for="choice in question.choices"
                    :key="choice.text"
                    :class="choice.chosen && !choice.correct ? 'wrong' : choice.correct ? 'correct' : ''">
                    {{ choice.text }}
                    <div class="overlay"></div>
                </li>
            </ol>
        </div>
        <router-link to="/">
            <button class="route-btn" @click="reset()">Back to Home</button>
        </router-link>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Review",
  methods: mapActions(["reset"]),
  computed: {
    ...mapGetters(["getSet", "correctQuestions"])
  }
};
</script>

<style lang="sass">
.review
    h1
        margin-bottom: 2rem
    .question
        margin-bottom: 1.5rem
        border-bottom: .25rem solid #5cbfe5
        ol
            list-style: lower-alpha
            margin-bottom: 1.5rem
            li
                padding: 1rem
                position: relative
                .overlay
                    position: absolute
                    z-index: -1
                    left: -2rem
                    top: 0
                    width: calc(100% + 2rem)
                    height: 100%
                &.correct, &.wrong
                    color: white
                &.correct .overlay
                    background: #388E3C
                &.wrong .overlay
                    background: #D50000
    button
        position: fixed
        left: 0
        bottom: 0
        width: 100%
        font-size: 1rem
        background: #5cbfe5
        color: white
        padding: 1rem
        border: none
</style>
