<template>
  <div class="discipline" @click="toLessons(title)">
    <img :src="require(`@/assets/images/${title}.jpg`)" alt="">
    <div class="title" v-text="title"></div>
  </div>
</template>

<script>
import store from "@/store/index.js";

export default {
  name: "DisciplineCard",
  props: {
    title: {
      type: String
    }
  },
  methods: {
    toLessons(title) {
      store.commit("setDiscipline", title);
      this.$router.push("/practice");
    }
  }
};
</script>

<style lang="sass" scoped>
.discipline
  display: flex
  align-items: flex-end
  width: calc(50% - .5rem)
  height: calc(((50vw - 1.5rem)/3)*4)
  border-radius: .5rem
  overflow: hidden
  margin-bottom: 1rem
  cursor: pointer
  position: relative
  &:nth-of-type(odd)
    margin-right: 1rem
  img
    position: absolute
    z-index: 1
    top: 0
    left: 0
    width: 100%
    height: 100%
  .title
    position: relative
    z-index: 2
    width: 100%
    text-align: center
    background: rgb(0, 0, 0, 0.66)
    color: white
    padding: .5rem 0
    border-bottom-left-radius: .5rem
    border-bottom-right-radius: .5rem
</style>
